
import { APIError } from "@/utils/loggers/apiLogger";
import { AxiosError, isAxiosError } from "axios";

/**
 * API    
 * @param error Axios  
 * @throws  
 */

export function handleAPIErrors(error: AxiosError): never {
	if (isAxiosError(error)) {
		const { response } = error;
		
		if (response) {
			//   
			if (response.status === 404) {
				APIError(`GitHub    : ${response.config.url}`);
				throw new Error(`GitHub    : ${response.config.url}`);
			}
			
			// API  
			if (response.status === 403) {
				const rateLimitRemaining = response.headers['x-ratelimit-remaining'];
				const rateLimitReset = response.headers['x-ratelimit-reset'];
				
				if (rateLimitRemaining === "0") {
					const resetDate = new Date(Number(rateLimitReset) * 1000);
					const errorMessage = `GitHub API  .   : ${resetDate.toLocaleString()}`;
					const customError = new Error(errorMessage);
					//     
					(customError as Error & { rateLimitReset: Date }).rateLimitReset = resetDate;
					APIError(errorMessage);
					throw customError;
				}
			}
			
			throw new Error(`GitHub API  : ${response.status} - ${response.statusText}`);
		}
	}
	
	throw error;
}

